@import url(https://fonts.googleapis.com/css2?family=Kanit&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1, h2, h3 {
  font-family: 'Kanit', sans-serif;
}
.product-container {
  display: inline-block;
  border-radius: 3px;
  background-color: white;
  box-shadow: 0 0.0625rem 0.125rem 0 rgba(0,0,0,.1);
  width: 12rem;
  margin: 7px 7px;
  cursor: pointer;
}
.product-detail {
  padding: 8px;
  word-wrap: break-word;
  font-size: 13px;
}
.product-schema {
  display: flex;
  justify-content: space-between;
}
.product-schema .product-price {
  color: #de3435;
  font-weight: bold;
  font-size: 15px;
}
img {
  width: 100%;
  height: 200px;
}
.product-cart-container {
    position: fixed;
    display: inline-block;
    right: 30px;
    bottom: 10px;
    width: 50px;
    height: 50px;
    box-shadow: 0 5px 7px 0 rgba(0,0,0,.1);
    border-radius: 50%;
    background-color: gray;
    color: white;
    font-size: 25px;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
}
.footer-container {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  background-color: #343a40;
  height: 150px;
  padding: 15px;
}
.footer-container div {
  width: 200px;
  color: orange;
  margin: 0 15px;
}
.footer-container div hr {
  margin: 5px 0;
}
.about-us {
  color: white;
  word-wrap: break-word;
}
.carousel {
    height: 350px;
}

.carousel-inner img {
    height: -webkit-fill-available;
}
.carousel-caption {
    bottom: 50px;
}
.jumbotron{
    height: 130px;
    margin-bottom: 0;
    padding: 1rem 0;
    background-color: #343a40;
}
.brand-header {
    display: inline-block;
    color: white;
}
.breadcrumb {
    margin: 0;
    padding: 0 1rem;
}
.search-bar {
    width: 40% !important;
    margin-right: 0.5rem; 
}
.search-btn {
    font-size: 1.3em;
    cursor: pointer;
}
.dropdown-toggle {
    color: white;
}
@media only screen and (max-width: 470px) {
    .brand-header {
        font-size: 1.2rem;
    }
    .jumbotron {
        height: 110px;
        padding: 2rem 0;
    }
}
@media only screen and (max-width: 775px) {
    .search-bar {
        width: 90% !important;
        margin-right: 0.5rem;
    }
    .nav-item {
        display: none;
    }
}
  
.product-list-view {
  display: flex !important;
  justify-content: space-between !important;
}
.remove-item-btn {
  cursor: pointer;
}

.App {
  background-color: #f3f3f3;
}
.product-list-container {
  display: block;
  padding: 2%;
}
.products-container {
  margin: 0 auto;
  width: 87%
}

