  background-color: #343a40;
  height: 150px;
  padding: 15px;
}
.footer-container div {
  width: 200px;
  color: orange;
  margin: 0 15px;
}
.footer-container div hr {
  margin: 5px 0;
}
.about-us {
  color: white;
  word-wrap: break-word;
}