.jumbotron{
    height: 130px;
    margin-bottom: 0;
    padding: 1rem 0;
    background-color: #343a40;
}
.brand-header {
    display: inline-block;
    color: white;
}
.breadcrumb {
    margin: 0;
    padding: 0 1rem;
}
.search-bar {
    width: 40% !important;
    margin-right: 0.5rem; 
}
.search-btn {
    font-size: 1.3em;
    cursor: pointer;
}
.dropdown-toggle {
    color: white;
}
@media only screen and (max-width: 470px) {
    .brand-header {
        font-size: 1.2rem;
    }
    .jumbotron {
        height: 110px;
        padding: 2rem 0;
    }
}
@media only screen and (max-width: 775px) {
    .search-bar {
        width: 90% !important;
        margin-right: 0.5rem;
    }
    .nav-item {
        display: none;
    }
}
  